/* Banner. */
#banner {
  color: #ffffff;
  background-color: $theme-dark;
  border-bottom: 1px solid $theme-dark;
  img {
    max-width: 1318px;
  }
  h1 {
    margin: 0;
    padding: .2em;
    font-size: 2.2em;
    font-weight: normal;
  }
}

/* Menu. */
#menu {
  margin: 5px 0 0 0; // top right bottom left

  a:link, a:visited {
    color: $theme-dark;
  }
  a:hover, a:active {
    color: #ffffff;
    background-color: $theme-dark;
  }
  a.current{
    background: $theme-dark;
    color: #ffffff;
  }

  ul {
    list-style-type: none; // remove bullets
    text-align: center;
    margin: 0;
    padding: 0;
    li {
      min-width: 120px;
      width: auto;
      font-size: 1.4em;
      display: block;
      float: left;
      margin: 1px;
      a {
        display: block;
        text-decoration: none;
        text-transform: uppercase;
        padding: 3px;
      }
    }
  }
  :after{
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
}

/* Responsive setting for menu. */
@media screen and (max-width: 600px) {
  #menu {
    ul li {
      float: none;
      width: auto;
      font-size: 1.2em;
    }
    :after {
      display: none
    }
  }
}

/* Content. */
#content {
  margin: 10px auto; // top&bottom left&right
  min-width: 25em;
  max-width: 33em;
  font-size: 16px;
}

/* Blank space between content footer. */
#clear {
  clear: both;
  height: 1em;
}

/* Footer. */
#footer {
  clear: both;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 2.6em; // hardcoded
  width: 100%;
  border-top: 1px solid $theme-dark;
  background-color: $theme-dark;
  #inner {
    margin: .2em;
    text-align: right;
    color: #ffffff;
  }
}
